
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'Header',
  props: {},
  data() {
    return {
      searchValue: '', //搜索组件
      a: 1,
      nav: [
        {
          title: '首页',
          router: '/'
        },
        {
          title: '开发文档',
          router: '/home'
        },
        {
          title: '视频教程',
          url: 'https://www.baidu.com/'
        },
        {
          title: '社区',
          url: 'https://www.baidu.com/'
        },
        {
          title: '支持我们',
          router: '/support'
        }
      ]
    };
  },
  methods: {
    handleNavClick(item){
      if(item.title === "开发文档"){
        sessionStorage.setItem('activeMenu','Topology');
      }
    }
  },
});
